<template>
  <div>
    <div>
      <form @submit.prevent>
        <!-- <div id="rightPage" class="col-lg-5">
                  <div class="rightContent"> -->
        <h3 class="mr-b-20 text-center">Meal Details</h3>
        <hr>
        <div class="row">
          <div class="col-md-12">
            <h4>Monday</h4>
          </div>
          <div class="form-group col-xs-6">
            <select class="form-control" required="required" v-model="monday.lunch">
              <option disabled value="">-- Select Lunch --</option>
              <option v-for="m in meals.monday" v-if="m.type === 'lunch'" :value="m.mkoo_meal_id">
                {{ m.related.meal[0].name }}
              </option>
            </select>
          </div>
          <div class="form-group col-xs-6">
            <select class="form-control" required="required" v-model="monday.dinner">
              <option disabled value="">-- Select Dinner --</option>
              <option v-for="m in meals.monday" v-if="m.type === 'dinner'" :value="m.mkoo_meal_id">
                {{ m.related.meal[0].name }}
              </option>
            </select>
          </div>
        </div>
        <div class="row">
          <div class="col-md-12">
            <h4>Tuesday</h4>
          </div>
          <div class="form-group col-xs-6">
            <select class="form-control" required="required" v-model="tuesday.lunch">
              <option disabled value="">-- Select Lunch --</option>
              <option v-for="m in meals.tuesday" v-if="m.type === 'lunch'" :value="m.mkoo_meal_id">
                {{ m.related.meal[0].name }}
              </option>
            </select>
          </div>
          <div class="form-group col-xs-6">
            <select class="form-control" required="required" v-model="tuesday.dinner">
              <option disabled value="">-- Select Dinner --</option>
              <option v-for="m in meals.tuesday" v-if="m.type === 'dinner'" :value="m.mkoo_meal_id">
                {{ m.related.meal[0].name }}
              </option>
            </select>
          </div>
        </div>
        <div class="row">
          <div class="col-md-12">
            <h4>Wednesday</h4>
          </div>
          <div class="form-group col-xs-6">
            <select class="form-control" required="required" v-model="wednesday.lunch">
              <option disabled value="">-- Select Lunch --</option>
              <option v-for="m in meals.wednesday" v-if="m.type === 'lunch'" :value="m.mkoo_meal_id">
                {{ m.related.meal[0].name }}
              </option>
            </select>
          </div>
          <div class="form-group col-xs-6">
            <select class="form-control" required="required" v-model="wednesday.dinner">
              <option disabled value="">-- Select Dinner --</option>
              <option v-for="m in meals.wednesday" v-if="m.type === 'dinner'" :value="m.mkoo_meal_id">
                {{ m.related.meal[0].name }}
              </option>
            </select>
          </div>
        </div>
        <div class="row">
          <div class="col-md-12">
            <h4>Thursday</h4>
          </div>
          <div class="form-group col-xs-6">
            <select class="form-control" required="required" v-model="thursday.lunch">
              <option disabled value="">-- Select Lunch --</option>
              <option v-for="m in meals.thursday" v-if="m.type === 'lunch'" :value="m.mkoo_meal_id">
                {{ m.related.meal[0].name }}
              </option>
            </select>
          </div>
          <div class="form-group col-xs-6">
            <select class="form-control" required="required" v-model="thursday.dinner">
              <option disabled value="">-- Select Dinner --</option>
              <option v-for="m in meals.thursday" v-if="m.type === 'dinner'" :value="m.mkoo_meal_id">
                {{ m.related.meal[0].name }}
              </option>
            </select>
          </div>
        </div>
        <div class="row">
          <div class="col-md-12">
            <h4>Friday</h4>
          </div>
          <div class="form-group col-xs-6">
            <select class="form-control" required="required" v-model="friday.lunch">
              <option disabled value="">-- Select Lunch --</option>
              <option v-for="m in meals.friday" v-if="m.type === 'lunch'" :value="m.mkoo_meal_id">
                {{ m.related.meal[0].name }}
              </option>
            </select>
          </div>
          <div class="form-group col-xs-6">
            <select class="form-control" required="required" v-model="friday.dinner">
              <option disabled value="">-- Select Dinner --</option>
              <option v-for="m in meals.friday" v-if="m.type === 'dinner'" :value="m.mkoo_meal_id">
                {{ m.related.meal[0].name }}
              </option>
            </select>
          </div>
        </div>
        <div class="row">
          <div class="col-md-12">
            <h4>Saturday</h4>
          </div>
          <div class="form-group col-xs-6">
            <select class="form-control" required="required" v-model="saturday.lunch">
              <option disabled value="">-- Select Lunch --</option>
              <option v-for="m in meals.saturday" v-if="m.type === 'lunch'" :value="m.mkoo_meal_id">
                {{ m.related.meal[0].name }}
              </option>
            </select>
          </div>
          <div class="form-group col-xs-6">
            <select class="form-control" required="required" v-model="saturday.dinner">
              <option disabled value="">-- Select Dinner --</option>
              <option v-for="m in meals.saturday" v-if="m.type === 'dinner'" :value="m.mkoo_meal_id">
                {{ m.related.meal[0].name }}
              </option>
            </select>
          </div>
        </div>
        <div class="row" v-show="meals.length !== 0">
          <div class="form-group mr-t-20 col-md-12 text-center">
            <button @click="addData(0)" v-if="editMode" class="btn btn-primary">Submit Order</button>
            <button @click="addData(1)" v-else class="btn btn-primary">Update Order</button>
          </div>
        </div>
        <!--</div>
              </div>-->
      </form>
    </div>
    <div class="row loading" v-show="loading">Loading&#8230;</div>
  </div>

</template>

<script>
  import Devless from './utils/devless'
  import _ from 'lodash'

  export default {
    props: ['token', 'details', 'mealitems'],
    data() {
      return {
        editMode: true,
        loading: false,
        monday: {
          lunch: '',
          dinner: ''
        },
        tuesday: {
          lunch: '',
          dinner: ''
        },
        wednesday: {
          lunch: '',
          dinner: ''
        },
        thursday: {
          lunch: '',
          dinner: ''
        },
        friday: {
          lunch: '',
          dinner: ''
        },
        saturday: {
          lunch: '',
          dinner: ''
        },
        meals: []
      }
    },
    methods: {
      async fetchData() {
        // this.token = window.location.search.substr(9)
          const res = await Devless.addData('mkoo', 'meal_query', { token: this.token, email: this.details.email })
          if (res.status_code === 625) {
            if(res.payload.related.old_choice.length !== 0) {
              const selected_items = _.groupBy(res.payload.related.old_choice, 'day');
              _.forEach(selected_items, function(val, key) {
                selected_items[key] = _.groupBy(selected_items[key], 'type');
              })
              this.monday.lunch = selected_items.monday.lunch[0].mkoo_meal_id;
              this.monday.dinner = selected_items.monday.dinner[0].mkoo_meal_id;
              this.tuesday.lunch = selected_items.tuesday.lunch[0].mkoo_meal_id;
              this.tuesday.dinner = selected_items.tuesday.dinner[0].mkoo_meal_id;
              this.wednesday.lunch = selected_items.wednesday.lunch[0].mkoo_meal_id;
              this.wednesday.dinner = selected_items.wednesday.dinner[0].mkoo_meal_id;
              this.thursday.lunch = selected_items.thursday.lunch[0].mkoo_meal_id;
              this.thursday.dinner = selected_items.thursday.dinner[0].mkoo_meal_id;
              this.friday.lunch = selected_items.friday.lunch[0].mkoo_meal_id;
              this.friday.dinner = selected_items.friday.dinner[0].mkoo_meal_id;
              this.saturday.lunch = selected_items.saturday.lunch[0].mkoo_meal_id;
              this.saturday.dinner = selected_items.saturday.dinner[0].mkoo_meal_id;

              this.editMode = !this.editMode
            }

            this.meals = _.groupBy(res.payload, 'day');
            this.loading = false;
            return
          } else {
            alert('Error fetching meals')
          }
      },
      async addData(method) {
        let orders = {
          "link": this.token,
          "email": this.details.email,
          "name": this.details.name,
          "ordered_meals": [
            {
              "day": "monday",
              "type": "lunch",
              "mkoo_meal_id": this.monday.lunch
            },
            {
              "day": "monday",
              "type": "dinner",
              "mkoo_meal_id": this.monday.dinner
            },
            {
              "day": "tuesday",
              "type": "lunch",
              "mkoo_meal_id": this.tuesday.lunch
            },
            {
              "day": "tuesday",
              "type": "dinner",
              "mkoo_meal_id": this.tuesday.dinner
            },
            {
              "day": "wednesday",
              "type": "lunch",
              "mkoo_meal_id": this.wednesday.lunch
            },
            {
              "day": "wednesday",
              "type": "dinner",
              "mkoo_meal_id": this.wednesday.dinner
            },
            {
              "day": "thursday",
              "type": "lunch",
              "mkoo_meal_id": this.thursday.lunch
            },
            {
              "day": "thursday",
              "type": "dinner",
              "mkoo_meal_id": this.thursday.dinner
            },
            {
              "day": "friday",
              "type": "lunch",
              "mkoo_meal_id": this.friday.lunch
            },
            {
              "day": "friday",
              "type": "dinner",
              "mkoo_meal_id": this.friday.dinner
            },
            {
              "day": "saturday",
              "type": "lunch",
              "mkoo_meal_id": this.saturday.lunch
            },
            {
              "day": "saturday",
              "type": "dinner",
              "mkoo_meal_id": this.saturday.dinner
            }
          ]
        };

        $('button').button('loading')
        let res = undefined;
        if(method === 0) {
          res = await Devless.addData('mkoo', 'place_order', orders)
        } else {
          res = await Devless.addData('mkoo', 'update_order', orders)
        }
        console.log(res)
        
        alert(res.message)
        
      }
    },
    mounted() {
      this.loading = true;
      this.fetchData();
    }
  }
</script>

<style scoped>
/* Absolute Center Spinner */
.loading {
  position: fixed;
  z-index: 999;
  height: 2em;
  width: 2em;
  overflow: show;
  margin: auto;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

/* Transparent Overlay */
.loading:before {
  content: '';
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.3);
}

/* :not(:required) hides these rules from IE9 and below */
.loading:not(:required) {
  /* hide "loading..." text */
  font: 0/0 a;
  color: transparent;
  text-shadow: none;
  background-color: transparent;
  border: 0;
}

.loading:not(:required):after {
  content: '';
  display: block;
  font-size: 10px;
  width: 1em;
  height: 1em;
  margin-top: -0.5em;
  -webkit-animation: spinner 1500ms infinite linear;
  -moz-animation: spinner 1500ms infinite linear;
  -ms-animation: spinner 1500ms infinite linear;
  -o-animation: spinner 1500ms infinite linear;
  animation: spinner 1500ms infinite linear;
  border-radius: 0.5em;
  -webkit-box-shadow: rgba(255, 255, 255, 1) 1.5em 0 0 0, rgba(255, 255, 255, 1) 1.1em 1.1em 0 0, rgba(255, 255, 255, 1) 0 1.5em 0 0, rgba(255, 255, 255, 1) -1.1em 1.1em 0 0, rgba(0, 0, 0, 0.5) -1.5em 0 0 0, rgba(0, 0, 0, 0.5) -1.1em -1.1em 0 0, rgba(255, 255, 255, 1) 0 -1.5em 0 0, rgba(255, 255, 255, 1) 1.1em -1.1em 0 0;
  box-shadow: rgba(255, 255, 255, 1) 1.5em 0 0 0, rgba(255, 255, 255, 1) 1.1em 1.1em 0 0, rgba(255, 255, 255, 1) 0 1.5em 0 0, rgba(255, 255, 255, 1) -1.1em 1.1em 0 0, rgba(255, 255, 255, 1) -1.5em 0 0 0, rgba(255, 255, 255, 1) -1.1em -1.1em 0 0, rgba(255, 255, 255, 1) 0 -1.5em 0 0, rgba(255, 255, 255, 1) 1.1em -1.1em 0 0;
}

/* Animation */

@-webkit-keyframes spinner {
  0% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@-moz-keyframes spinner {
  0% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@-o-keyframes spinner {
  0% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes spinner {
  0% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
</style>


