<template>
  <div id="app">
    <div class="container-fluid">
      <div class="row">
        <div id="leftPage" class="col-lg-7 pd-tb-mini pd-r-50">
          <h1>MEST Meals Order System</h1>
        </div>
        <div style="">
          <div id="rightPage" class="col-lg-5">
					  <div class="rightContent">
              <order-view :token="token" v-if="state === 1"  v-on:changeState="changeState" :mealitems="meals" :details="details"></order-view>
              <details-view :token="token" v-show="state === 0" v-on:changeState="changeState" :mealitems="meals"></details-view>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import OrderView from './OrderView';
  import DetailsView from './DetailsView';
  import Devless from './utils/devless';
  import _ from 'lodash'

  export default {
    name: 'app',
    components: {
      OrderView,
      DetailsView
    },
    data() {
      return {
        meals: undefined,
        state: 0,
        details: undefined,
        token: undefined
      }
    },
    methods: {
      changeState(state, obj) {
        this.state = state;
        this.details = obj;
      },
      async fetchData() {
        const res = await Devless.addData('mkoo', 'meal_query', { token: this.token })
        console.log(res)
        if (res.status_code === 625) {
          this.meals = _.groupBy(res.payload, 'day');
          // console.log(this.meals)
          return
        } else {
          alert('Incorrect link')
        }
      }
    },
    created() {
      this.token = window.location.search.substr(9)
      // this.fetchData()
    }
  }
</script>


